$header-primary-colour: $primary-colour;

header {

	background-image: url("img/static/header-background.jpg");
	background-position: center;
	background-size: cover;
	background-repeat: no-repeat;

	border-bottom: 13px solid $primary-colour;

	&:before {

		content: "";
		position: absolute;
		display: block;

		width: 100%;
		height: 100px;

		background: -moz-linear-gradient(top, rgba(0,0,0,0.65) 0%, rgba(0,0,0,0) 100%) /* FF3.6-15 */;
		background: -webkit-linear-gradient(top, rgba(0,0,0,0.65) 0%,rgba(0,0,0,0) 100%) /* Chrome10-25,Safari5.1-6 */;
		background: linear-gradient(to bottom, rgba(0,0,0,0.65) 0%,rgba(0,0,0,0) 100%) /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */;
		filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#a6000000', endColorstr='#00000000',GradientType=0 ) /* IE6-9 */;

		@include md {

			height: 360px; } }

	.container {

		padding: 24px 35px;

		@include md {

			position: relative; }

		.inner-container {

			position: relative;

			img.logo {

				position: absolute;
				width: 160px;
				top: 0;
				left: 0;

				@include md {

					top: 20px;
					width: 260px; } } } }

	.head-slider {

		padding-top: 80px;
		height: 270px;
		overflow: hidden;

		@include md {

			padding-top: 200px;
			height: 640px; }

		.slide {

			h1 {

				font-size: 23px;
				font-weight: 800;
				color: white;
				margin: 0;

				@include md {

					font-size: 48px; } }

			p {

				color: white;
				font-size: 13px;
				margin: 12px 0;

				@include md {

					font-size: 19px;
					margin: 21px 0; } }

			button {

				max-width: 40%;
				border-radius: 0;
				padding: 6px 12px;
				margin-top: 16px;
				font-weight: 600;

				@include md {

					padding: 10px 21px;
					font-size: 18px; }

				&:hover {

					background: black; }

				*:empty {

					display: none; } }

			img {

				float: right;
				max-width: 150px;
				margin-top: 10px;

				@include md {

					float: left;
					clear: left;
					max-width: 360px;
					margin-top: 50px; } }

			&:after {

				content: "";
				display: table;
				clear: both; } }

		.arrow {

			position: absolute;
			top: 50%;

			width: 22px;
			height: 22px;

			background-position: center;
			background-size: contain;
			background-repeat: no-repeat;

			cursor: pointer;

			@include md {

				width: 32px;
				height: 32px; }

			&.left {

				left: -30px;
				background-image: url("../img/static/arrow-slider-left.png");

				@include md {

					left: -60px; } }

			&.right {

				right: -30px;
				background-image: url("../img/static/arrow-slider-right.png");

				@include md {

					right: -60px; } } } } }
